@import url(https://fonts.googleapis.com/css?family=News+Cycle&display=swap);
:root {
  font-size: 20px;
}

html,
body {
  margin: 0;
  padding: 0;
  background: #ffbf66;
  color: #fff7c7;
  overflow: hidden;
  font-family: "News Cycle", sans-serif;
}

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.canvas > div {
  z-index: 1;
}

.loading {
  padding: 10px;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}

.scrollArea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

